html {
  scroll-behavior: smooth;
}

.container-slider {
  max-width: 97%;
  padding-top: 2rem;
  padding-inline: 1rem;
}

.swiper_container {
  height: 28rem;
  padding: 2rem 0;
  position: relative;
}

.carousel_container {
  height: min-content;
  padding: 2rem 1rem;
  padding-top: 4rem;
  position: relative;
}

.swiper-slide {
  visibility: hidden;
  /* width: min-content; */
  height: min-content;
  position: relative;
}

.swiper-slide.swiper-slide-visible {
  visibility: visible;
}

.swiper-slide.swiper-slide-active {
  visibility: visible;
}

.swiper-slide.swiper-slide-next {
  visibility: visible;
}

.swiper-slide.swiper-slide-prev {
  visibility: visible;
}

.swiper-slide img {
  width: 100%;
  height: 100%;
  border-radius: 2rem;
  object-fit: contain;
}

.swiper-slide-shadow-left,
.swiper-slide-shadow-right {
  display: none;
}

.slider-controler {
  position: relative;
  bottom: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 959.95px) {
  .container-slider {
    max-width: 100%;
    /* transform: scale(0.5); */
  }
}

@media (min-width:1280px) {
  .css-162drrz-MuiContainer-root{
    max-width:none !important;
  }
}
